@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Poppins", sans-serif;
  background: #eee;
}

.wrapper {
  margin-top: 12%;
  margin-left: 30%;
  max-width: 580px;
  width: 100%;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  padding: 1rem;
  background-color: rgb(236, 181, 0);
}
.message-wrapper {
  width: 100%;
  padding: 0 1rem;
  display: none;
}
.message {
  margin: 0.5rem 0rem;
  background: #212121;
  color: #eaeaea;
  padding: 1rem 1rem;
  margin-top: 1.5rem;
  font-weight: 400;
  font-size: 13px;
  width: 100%;
  border-radius: 13px;
}

.main-heading {
  color: #212121;
  text-transform: capitalize;
  margin-bottom: 0.5rem;
}
.sub-text {
  color: #424242;
  font-size: 15px;
}

.form-wrapper {
  /* display: flex; */
  width: 150%;
  text-align: center;
  padding: 1rem;
}

.input-field {
  display: flex;
  align-items: center;
  justify-content: center;
  /* width: 25%; */
  padding: 0.5rem 0rem;
}
.input-field-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* width: 25%; */
  padding: 0.5rem 0rem;
}
.input-field label {
  color: rgb(21, 6, 226);
  font-size: 23px;
}
.input-field input {
  display: flex;
  width: 5%;
  padding: .75rem;
  border-radius: 13px;
  background: #ffff;
  border: 2px solid #ccc;
  margin: 0.5rem;
  color: #000;
  font-family: inherit;
  text-align: center;
}
.input-field-login input {
  display: flex;
  width: 50%;
  padding: .75rem;
  border-radius: 13px;
  background: #ffff;
  border: 2px solid #ccc;
  margin: 0.5rem;
  color: #000;
  font-family: inherit;
  text-align: center;
}
input:focus {
  border: 2px solid #2193b0;
}

.form-wrapper button,
.main-button {
  background: linear-gradient(to right, #0245ff, #1a02a5);
  color: #fff;
  width: 50%;
  padding: 0.75rem;
  font-weight: 600;
  font-family: inherit;
  margin-top: 0.75rem;
  outline: none;
  border: none;
  border-radius: 13px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 18px;
}
.form-wrapper button:hover {
  opacity: 0.9;
}

.bottom-message {
  width: 100%;
  text-align: center;
  color: #ccc;
  margin-top: 2rem;
}
.bottom-message h5 {
  font-weight: 400;
}
.bottom-message h5 a {
  color: #fff;
  font-weight: 600;
  text-decoration: none;
}

#partitioned {
  padding-left: 15px;
  letter-spacing: 42px;
  border: 0;
  background-image: linear-gradient(to left, black 70%, rgba(255, 255, 255, 0) 0%);
  background-position: bottom;
  background-size: 50px 1px;
  background-repeat: repeat-x;
  background-position-x: 35px;
  width: 220px;
}

#divInner{
  left: 0;
  position: sticky;
}

#divOuter{
  width: 190px; 
  overflow: hidden;
}
hr{
  width: 60%;
}